<template>
  <div
    class="question"
  >
    <div class="thank" v-if="finish">
      <h1>Se generó la solicitud {{this.ruleForm._nro_hoja_reclamacion}}</h1>
      <p>Estamos trabajando para mejorar tu experiencia</p>
      <el-row style="margin-top: 20px">
        <el-col :md="8" style="margin-top:10px">
          <a :href="urlPdfDescargar" target="_blank">Descargar en formato PDF</a>
        </el-col>
        <el-col :md="16">
          <el-row>
            <el-col :md="16">
              <el-input @keyup.enter.native="enviarCorreo()" v-model="enailEnviar"/>
            </el-col>
            <el-col :md="8">
              <el-button v-on:click="enviarCorreo()">Enviar correo</el-button>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <div class="question-container" v-else >
      <div class="cabecera">
        <div class="item"><div class="item-image"><img src="../../assets/images/logo-muni-azul.png"></div></div>
        <div class="item">
          <div class="item-texto"><label>Libro de reclamaciones</label></div>
          <div class="item-texto"><label>(D.S. N° 007-2020-PCM)</label></div>
        </div>
      </div>
      <el-row :gutter="10">
        <el-col :xs="18" :md="5" style="text-align:left">
          <label>N° de hoja de reclamación:</label>
        </el-col>
        <el-col :xs="6" :md="3">
          <el-input v-model="ruleForm._nro_hoja_reclamacion" disabled/>
        </el-col>
      </el-row>
      <div class="encabezado --dos">
        <el-row :gutter="10" >
          <el-col :xs="24" :sm="8">
            <label>Nombre de la sede</label>
            <el-select v-model="ruleForm._id_sede" @change="escogerSede(ruleForm._id_sede)" placeholder="Seleccione">
              <el-option
                v-for="local of listaLocales"
                :key="local.idElemento"
                :label="local.nombreSede"
                :value="local.idElemento"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :sm="8">
            <label>Dirección de la sede</label>
            <el-input disabled v-model="ruleForm._direccion_sede"></el-input>
          </el-col>
          <el-col :sm="8">
          <label>Fecha de registro</label>
          <el-input v-model="ruleForm._fechaRegistro" disabled ></el-input>
          </el-col>
        </el-row>
      </div>
      <h1><u>REGISTRO - HOJA DE RECLAMACIÓN</u></h1>
      <p class="detalle">
        Lamentamos el malestar generado. Para darle una respuesta oportuna por
        favor complete la siguiente información
      </p>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-card class="box-card">
          <div slot="header" style="text-align: left" class="clearfix">
            <span>I. CUÉNTANOS ¿QUE SUCEDIÓ?</span>
          </div>
          <el-form-item
            label="1.- ¿El problema ocurrió de manera presencial?"
            prop="_1_1_modalidad"
          >
            <el-row>
              <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" class="lista-positivo">
                <div class="lista-positivo-hijo">
                  <el-radio-group v-model="ruleForm._1_1_modalidad">
                    <el-radio label="PRESENCIAL">Si</el-radio>
                  </el-radio-group>
                </div>
              </el-col>
              <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
                <div class="lista-negativo">
                  <el-row>
                    <i class="detalle">Si tu respuesta fue "NO" marque una alternativa</i>
                  </el-row>
                  <el-row>
                    <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                      <div class="lista-no">
                        <label style="margin: auto">NO</label>
                      </div>
                    </el-col>
                    <el-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
                      <el-row :gutter="10">
                        <div class="lista-checkbox">
                          <el-radio-group v-model="ruleForm._1_1_modalidad">
                            <el-radio label="VIRTUAL TELEFONO">Teléfono</el-radio>
                            <el-radio label="VIRTUAL PÁGINA INSTITUCIONAL">Página Institucional</el-radio>
                            <el-radio label="VIRTUAL CORREO ELECTRÓNICO">Correo Electrónico</el-radio>
                            <el-radio label="VIRTUAL CHAT">Chat</el-radio>
                            <el-radio label="VIRTUAL RED SOCIAL">Red Social</el-radio>
                          </el-radio-group>
                        </div>
                      </el-row>
                    </el-col>
                  </el-row>
                </div>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item
            label="2.- ¿Cuando ocurrió la situación que origina el presente reclamo?"
            prop="_1_2_fecha"
          >
            <el-input
              v-model="ruleForm._1_2_fecha"
              placeholder="Ingrese Fecha"
              maxlength="30"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="3.- ¿Aproximadamente a que hora sucedió?"
            prop="_1_3_hora"
          >
            <el-input
              v-model="ruleForm._1_3_hora"
              placeholder="Ingrese hora aproximada"
              maxlength="30"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="4.- Describenos ¿qué sucedió?"
            prop="_1_4_descripcion"
          >
            <el-input
              v-model="ruleForm._1_4_descripcion"
              type="textarea"
              placeholder="Describre lo que sucedió"
              :autosize="{ minRows: 3, maxRows: 9 }"
              maxlength="1000"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-card>
        <el-card class="box-card">
          <div slot="header" style="text-align: left" class="clearfix">
            <span>II. INFORMACION ADICIONAL</span>
          </div>
          <el-form-item
            label="1.- Identifica el motivo del reclamo. Puedes seleccionar máximo dos opciones"
            prop="_2_1_motivo_array"
          >
            <div class="lista-check">
              <el-checkbox-group v-model="ruleForm._2_1_motivo_array" :max="2">
                <el-row :gutter="10">
                  <el-checkbox
                    el-checkbox
                    label="Trato profesional en la atención"
                    name="type"
                  >
                    <div class="texto-checkbox-title">
                      Trato profesional en la atención:
                    </div>
                    <div class="texto-checkbox-content">
                      (La persona que te atendió no lo hizo de forma adecuada)
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox label="Tiempo" name="type">
                    <div class="texto-checkbox-title">Tiempo:</div>
                    <div class="texto-checkbox-content">
                      (Hubo demora antes y/o durante la atención que recibiste)
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox label="Procedimiento" name="type">
                    <div class="texto-checkbox-title">Procedimiento:</div>
                    <div class="texto-checkbox-content">
                      (No se siguió el procedimiento de atención o no estás de
                      acuerdo con este)
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox label="Información" name="type">
                    <div class="texto-checkbox-title">Información:</div>
                    <div class="texto-checkbox-content">
                      (La orientación sobre el servicio fue inadecuada,
                      insuficiente 0 imprecisa)
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox label="Resultado" name="type">
                    <div class="texto-checkbox-title">Resultado:</div>
                    <div class="texto-checkbox-content">
                      (No se pudo obtener un resultado concreto como parte del<br>
                      servicio y/o no se justifica la negativa en la atención
                      del servicio.)
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox label="Confianza:" name="type">
                    <div class="texto-checkbox-title">Confianza:</div>
                    <div class="texto-checkbox-content">
                      (Ocurrió una situación que afectó la confianza y
                      credibilidad de la entidad)
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox label="Disponibilidad:" name="type">
                    <div class="texto-checkbox-title">Disponibilidad:</div>
                    <div class="texto-checkbox-content">
                      (El medio de atención (virtual, presencial o telefónico)<br>
                      por el que se brinda el servicio no responde a tus<br>
                      expectativas o tiene horarios res trin gidos. )
                    </div>
                  </el-checkbox>
                </el-row>
                <el-row :gutter="10">
                  <el-checkbox el-checkbox label="Infraestructura:" name="type">
                    <div class="texto-checkbox-title">Infraestructura:</div>
                    <div class="texto-checkbox-content">
                      (El ambiente en e/ que se realizó la atención y/o
                      mobiliario no están en buen estado,<br> no hay rutas
                      accesibles que faciliten el desplazamiento de las personas<br>
                      o el local queda en un sitio inseguro)
                    </div>
                  </el-checkbox>
                </el-row>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item
            label="2.- ¿Trataron de darte una solución previa al registro del reclamo?"
            prop="_2_2_solucion_previa"
          >
            <el-radio-group v-model="ruleForm._2_2_solucion_previa">
              <el-radio label="SI"></el-radio>
              <el-radio label="NO"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-card>
        <el-card class="box-card">
          <div slot="header" style="text-align: left" class="clearfix">
            <span>III. DATOS PERSONALES</span>
          </div>
          <el-form-item label="1.- Tipo de documento" prop="_3_1_tip_documento">
            <el-radio-group
              :disabled="disabledDatosPersonales"
              v-model="ruleForm._3_1_tip_documento"
            >
              <el-radio :label="31">DNI</el-radio>
              <el-radio :label="33">CARNET DE EXTRANJERÍA</el-radio>
              <el-radio :label="1335">PASAPORTE</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="2.- Nro. de documento"
            prop="_3_2_nro_documento"
            @change.native="obtenerUsuario()"
          >
            <el-input 
              :disabled="disabledDatosPersonales"
              v-model="ruleForm._3_2_nro_documento"
              placeholder="Nro. de documento"
              @keypress.native="soloNumeros"
              maxlength="13"
            ></el-input>
          </el-form-item>
          <el-form-item label="3.- Nombres y Apellidos" required>
            <el-col :md="8">
              <el-form-item prop="_3_3_name">
                <div class="datos-personales">
                  <el-input
                    :disabled="disabledDatosPersonales"
                    v-model="ruleForm._3_3_name"
                    placeholder="Nombres"
                    maxlength="30"
                  ></el-input>
                </div>
              </el-form-item>
            </el-col>
            <el-col :md="8">
              <el-form-item prop="_3_3_apePat">
                <div class="datos-personales">
                  <el-input
                    :disabled="disabledDatosPersonales"
                    v-model="ruleForm._3_3_apePat"
                    placeholder="Apellido Paterno"
                    maxlength="20"
                  ></el-input>
                </div>
              </el-form-item>
            </el-col>
            <el-col :md="8">
              <el-form-item prop="_3_3_apeMat">
                <div class="datos-personales">
                  <el-input
                    :disabled="disabledDatosPersonales"
                    v-model="ruleForm._3_3_apeMat"
                    placeholder="Apellido Materno"
                    maxlength="20"
                  ></el-input>
                </div>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item
            label="4.- Envío de la respuesta"
            prop="_3_4_tipo_respuesta"
          >
          <el-row>
            <el-col :md="24">
              <div class="lista-datos respuesta">
                  <el-radio-group
                    v-model="ruleForm._3_4_tipo_respuesta"
                    v-on:change="ruleForm._3_4_recurso_respuesta = ''"
                  >
                    <el-row>
                      <el-radio label="Quiero recoger una copia en sede">Quiero recoger una copia en sede</el-radio>
                    </el-row>
                    <el-row>
                      <el-radio label="DOMICILIO"
                        >Quiero que una copia sea enviada a mi domicilio</el-radio
                      >
                      <template v-if="ruleForm._3_4_tipo_respuesta == 'DOMICILIO'">
                        <el-form-item 
                    prop="_3_4_recurso_notificacion_domicilio">
                        <el-input
                          v-model="ruleForm._3_4_recurso_notificacion_domicilio"
                          placeholder="Ingrese su direccion"
                          maxlength="50"
                        ></el-input>
                        </el-form-item>
                      </template>
                    </el-row>
                    <el-row>
                      <el-radio label="CELULAR"
                        >Quiero recibirla por celular</el-radio
                      >
                      <template v-if="ruleForm._3_4_tipo_respuesta == 'CELULAR'">
                        <el-form-item
                    prop="_3_4_recurso_notificacion_celular"
                  ><el-input v-model="ruleForm._3_4_recurso_notificacion_celular" placeholder="Ingrese su nro. de celular" maxlength="9" @keypress.native="soloNumeros"></el-input>
                      </el-form-item>
                      </template>
                    </el-row>
                    <el-row>
                      <el-radio label="CORREO"
                        >Quiero recibirla por correo electrónico</el-radio
                      >
                      <template v-if="ruleForm._3_4_tipo_respuesta == 'CORREO'"
                        ><el-form-item
                    prop="_3_4_recurso_notificacion_email"
                  ><el-input
                          v-model="ruleForm._3_4_recurso_notificacion_email"
                          placeholder="Ingrese su correo electrónico"
                          maxlength="35"
                        ></el-input
                      ></el-form-item></template>
                    </el-row>
                  </el-radio-group>
              </div>
            </el-col>
          </el-row>
          </el-form-item>
          <el-form-item
            label="5.- Notificación del avance (opcional)"
            prop="_3_5_notifi_avance"
          >
          <el-row>
            <el-col :md="24">
            <label style="display: inline-block; font-size: 10px"
              >Selecciona cómo quieres recibir las notificaciones del avance de
              tu reclamo</label
            >
            <div class="lista-datos">
              <el-radio-group
                v-model="ruleForm._3_5_notifi_avance"
                v-on:change="ruleForm._3_5_recurso_notificacion = ''"
              >
                <el-row :gutter="10">
                  <el-radio label="correo">Correo Electrónico</el-radio>
                  <template v-if="ruleForm._3_5_notifi_avance == 'correo'"
                    >
                  <el-form-item
                    prop="_3_5_recurso_notificacion_email"
                  ><el-input
                      v-model="ruleForm._3_5_recurso_notificacion_email"
                      placeholder="Ingrese su correo electrónico"
                      maxlength="35"
                    ></el-input
                  ></el-form-item></template>
                </el-row>
                <el-row :gutter="10">
                <el-radio label="mensaje">Mensaje de Texto</el-radio>
                <template v-if="ruleForm._3_5_notifi_avance == 'mensaje'"
                  >
                  <el-form-item
                    prop="_3_5_recurso_notificacion_celular"
                  >
                    <el-input tyoe="_3_5_recurso_notificacion_celular"
                      v-model.number="ruleForm._3_5_recurso_notificacion_celular"
                      placeholder="Ingrese su nro. de celular"
                      maxlength="10"
                      @keypress.native="soloNumeros"
                    ></el-input>
                  </el-form-item>
                </template>
                </el-row>
              </el-radio-group>
            </div>
            </el-col>
            </el-row>
          </el-form-item>
        </el-card>
        <el-form-item>
          <template v-if="mostrarBotton">
          <el-button type="primary" @click="submitForm('ruleForm')"
            >Enviar</el-button
          >
          </template>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import scss from "../../assets/scss/formComponent.scss";
  import axios from "axios";
  import moment from "moment";
  import Constantes from "../../store/constantes.js";

  import 'sweetalert2/src/sweetalert2.scss'
  export default {
    data() {
      return {
        ruleForm: {
          _1_1_modalidad: "",
          _1_2_fecha: "",
          _1_3_hora: "",
          _1_4_descripcion: "",
          _2_1_motivo_array: [],
          _2_2_solucion_previa: "",
          _3_1_tip_documento: "",
          _3_2_nro_documento: "",
          _3_3_name: "",
          _3_3_apePat: "",
          _3_3_apeMat: "",
          _3_4_tipo_respuesta: "",
          _3_4_recurso_respuesta: "",
          _3_4_recurso_notificacion_domicilio: "",
          _3_4_recurso_notificacion_celular: "",
          _3_4_recurso_notificacion_email: "",
          _3_5_notifi_avance: "",
          _3_5_recurso_notificacion: "",
          _3_5_recurso_notificacion_celular: null,
          _3_5_recurso_notificacion_mail: null,
          _general_user_id: null,
          _id_sede: null,
          _nombre_sede: "",
          _fechaRegistro: moment(new Date()).format("DD/MM/YYYY"),
          _usuario_registra: "JULIO.TITO@MIRAFLORES.GOB.PE",
          _id_usuario_registra: 186,
          _direccion_sede: "",
          _nro_hoja_reclamacion: "",
        },
        enailEnviar: "",
        listaLocales: null,
        localSeleccionado: null,
        disabledDatosPersonales: false,
        loading: false,
        finish: false,
        mostrarBotton:true,
        urlPdfDescargar: "",
        cabecera: {
          sede: null,
          idSede: null,
          fecha: new Date(),
          direccionSede: null,
          nroHojaReclamacion: 101,
          _nro_hoja_reclamacion: null,
        },
        rules: {
          _1_1_modalidad: [
            { required: true, message: "Porfavor seleccione", trigger: "change" },
          ],
          _1_2_fecha: [
            {
              required: true,
              message: "Porfavor ingrese campo",
              trigger: "blur",
            },
            { min: 6, message: "Digite correctamente", trigger: "blur" },
          ],
          _1_3_hora: [
            {
              required: true,
              message: "Porfavor ingrese campo",
              trigger: "blur",
            },
            { min: 3, message: "Digite correctamente", trigger: "blur" },
          ],
          _1_4_descripcion: [
            {
              required: true,
              message: "Porfavor describa lo ocurrido",
              trigger: "blur",
            },
          ],
          _2_1_motivo_array: [
            {
              type: "array",
              required: true,
              message: "Por favor marque almenos uno",
              trigger: "change",
            },
          ],
          _2_2_solucion_previa: [
            { required: true, message: "Porfavor seleccione", trigger: "change" },
          ],
          _3_1_tip_documento: [
            { required: true, message: "Porfavor seleccione", trigger: "change" },
          ],
          _3_2_nro_documento: [
            { required: true, message: "Porfavor ingrese su nro de documento", trigger: "blur" },
            { min: 8 , message: "Faltan caracteres"}
          ],
          _3_3_name: [
            { required: true, message: "Ingrese su nombre", trigger: "blur" },
          ],
          _3_3_apePat: [
            {
              required: true,
              message: "Ingrese su apellido paterno",
              trigger: "blur",
            },
          ],
          _3_3_apeMat: [
            {
              required: true,
              message: "Ingrese su apellido materno",
              trigger: "blur",
            },
          ],
          _3_4_tipo_respuesta: [
            { required: true, message: "Porfavor seleccione", trigger: "change" },
          ],
          _3_4_recurso_notificacion_domicilio: [
            {required: true,  message: "Por favor ingrese su direccion.", trigger: "blur" },
          ],
          _3_4_recurso_notificacion_celular: [
            {required: true, message: "Por favor ingrese su nro. de celular.", trigger: "blur" },
          ],
          _3_4_recurso_notificacion_email: [
            {required: true,  message: "Por favor ingrese su correo.", trigger: "blur" },
            {
              type: "email",
              message: "El correo ingresado no es válido",
              trigger: ["blur", "change"],
            },
          ],
          _3_5_recurso_notificacion: [
            { message: "Porfavor seleccione", trigger: "change" },
          ],
          _3_5_recurso_notificacion_email: [
            { message: "Por favor ingrese su correo.", trigger: "blur" },
            {
              type: "email",
              message: "El correo ingresado no es válido",
              trigger: ["blur", "change"],
            },
          ],
        },
      };
    },
    created() {
      this.obtSedes();
      this.obtCorrelativo();
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.ruleForm == null) {
            } else {
              this.validaciones();
            }
          } else {
            this.$swal({
                icon: 'info',
                title: '',
                text: 'Faltan llenar variables'
            });
            return false;
          }
        });
      },
      escogerSede(idSede){
        this.localSeleccionado=this.listaLocales.find(element => element.idElemento==idSede);
        this.ruleForm._nombre_sede=this.localSeleccionado.nombreSede;
        this.ruleForm._direccion_sede=this.localSeleccionado.direccion;
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      validaciones(){
        if(this.ruleForm._3_2_nro_documento.trim()=='') return this.$swal({
            icon: 'info',
            title: '',
            text: 'Número de documento vacío'
        });
        else if(this.ruleForm._3_1_tip_documento.length == 31 && (this.ruleForm._3_2_nro_documento.trim().length!=8) ){
          return this.$swal({
            icon: 'info',
            title: '',
            text: 'Nro de documento inválido'
          });
        }
        else if( this.ruleForm._3_2_nro_documento.length == 33 && (this.ruleForm._3_2_nro_documento.trim().length<9||this.ruleForm._3_2_nro_documento.trim().length>11)){
          return this.$swal({
            icon: 'info',
            title: '',
            text: 'Nro de documento inválido'
          });
        }
        else if(this.ruleForm._3_2_nro_documento.length == 1335 && (this.ruleForm._3_2_nro_documento.trim().length<9 ||this.ruleForm._3_2_nro_documento.trim().length>11)){
          return this.$swal({
            icon: 'info',
            title: '',
            text: 'Nro de documento inválido'
          });
        }else if(this.ruleForm._id_sede == null){
          return this.$swal({
            icon: 'info',
            title: '',
            text: 'Falta escoger lugar donde ocurrió el incidente'
          });
        }else return this.sendDocumento()
      },
      obtenerUsuario() {
        if(this.ruleForm._3_2_nro_documento.trim()=='') {}
        else if(this.ruleForm._3_1_tip_documento.length == 31 && this.ruleForm._3_2_nro_documento.trim().length<8){}
        else if( this.ruleForm._3_2_nro_documento.length == 32 && this.ruleForm._3_2_nro_documento.trim().length<9){}
        else if(this.ruleForm._3_2_nro_documento.length == 1335 && this.ruleForm._3_2_nro_documento.trim().length<9){}
        else {
          const url = Constantes.API_PERSONA+"/obtenerpersonapornombredoc";
          let body = {
            documento: this.ruleForm._3_2_nro_documento,
          };
          axios
            .post(url, body)
            .then((response) => {
              if (response.data.data.length == 1) {
                this.ruleForm._3_3_name = response.data.data[0].NOM_NOMBRES;
                this.ruleForm._3_3_apePat =
                  response.data.data[0].NOM_APELLIDO_PATERNO;
                this.ruleForm._3_3_apeMat =
                  response.data.data[0].NOM_APELLIDO_MATERNO;
                this.disabledDatosPersonales = true;
                this.ruleForm._3_1_tip_documento =
                  response.data.data[0].IDE_TIPO_DOCUENTO;
                this.ruleForm._general_user_id = response.data.data[0].IDE_PERSONA;
              }
            })
            .catch((e) => console.log(e));
        }
      },
      sendDocumento() {
        const url = Constantes.rutaTramite+"libro-reclamaciones";
        this.ruleForm._2_1_cantidad_motivo = this.ruleForm._2_1_motivo_array.length;
        let resp = "";

        if (this.ruleForm._2_1_motivo_array.length == 1) {
          this.ruleForm._2_1_motivo = this.ruleForm._2_1_motivo_array[0].toUpperCase();
          this.ruleForm._2_1_motivo1 = this.ruleForm._2_1_motivo_array[0].toUpperCase();
        } else if (this.ruleForm._2_1_motivo_array.length == 2) {
          this.ruleForm._2_1_motivo =
            this.ruleForm._2_1_motivo_array[0].toUpperCase() +
            "||" +
            this.ruleForm._2_1_motivo_array[1].toUpperCase();
          this.ruleForm._2_1_motivo1 = this.ruleForm._2_1_motivo_array[0].toUpperCase();
          this.ruleForm._2_1_motivo2 = this.ruleForm._2_1_motivo_array[1].toUpperCase();
        }
        if(this.ruleForm._3_4_tipo_respuesta == 'CORREO'){
          this.ruleForm._3_4_recurso_respuesta = this.ruleForm._3_4_recurso_notificacion_email
        }else if(this.ruleForm._3_4_tipo_respuesta == 'DOMICILIO'){
          this.ruleForm._3_4_recurso_respuesta = this.ruleForm._3_4_recurso_notificacion_domicilio
        }else if(this.ruleForm._3_4_tipo_respuesta == 'CELULAR'){
          this.ruleForm._3_4_recurso_respuesta = this.ruleForm._3_4_recurso_notificacion_celular
        }else this.ruleForm._3_4_recurso_respuesta = ""
        if(this.ruleForm._3_4_recurso_respuesta=="")this.ruleForm._3_4_recurso_respuesta=this.ruleForm._3_4_tipo_respuesta
        this.ruleForm._3_3_apeMat = this.ruleForm._3_3_apeMat.toUpperCase()
        this.ruleForm._3_3_apePat = this.ruleForm._3_3_apePat.toUpperCase()
        this.ruleForm._3_3_name = this.ruleForm._3_3_name.toUpperCase()
        this.ruleForm._1_2_fecha = this.ruleForm._1_2_fecha.toUpperCase()
        this.ruleForm._1_3_hora = this.ruleForm._1_3_hora.toUpperCase()
        this.ruleForm._3_4_recurso_respuesta = this.ruleForm._3_4_recurso_respuesta.toUpperCase()
        this.ruleForm._3_5_recurso_notificacion = this.ruleForm._3_5_recurso_notificacion.toUpperCase()
        this.ruleForm._nombre_sede = this.ruleForm._nombre_sede.toUpperCase()
        this.ruleForm._direccion_sede = this.ruleForm._direccion_sede.toUpperCase()
        this.mostrarBotton=false
        this.abrirModalProcesando()
        axios.post(url, this.ruleForm).then((response) => {
          this.loading.close();
          if(response.data.success){
            this.finish=true
            this.urlPdfDescargar = response.data.urlArchivo
            this.$swal.fire({
              icon: 'success',
              title: 'Solicitud procesada con éxito',
              showConfirmButton: false,
              timer: 1500
            })
          }
        });
      },
      abrirModalProcesando(){
        this.loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
      },
      obtSedes() {
        const url = Constantes.rutaTramite+"locales";
        axios.get(url, {
          params: {
            idGrupoLocales: 393,
          },
        }).then((response) => {
          this.listaLocales = response.data.data;
        });
      },
      obtCorrelativo(){
        const url = Constantes.rutaTramite+"correlativo";
        axios.get(url, {
          params: {
            idTipoDocumento: 9391,
            anio: new Date().getFullYear()
          },
        }).then((response) => {
          this.ruleForm._nro_hoja_reclamacion = response.data.data
        });
      },
      enviarCorreo(){
        const url = Constantes.rutaTramite+"enviar-correo-libro-reclamaciones"
        axios.get(url, {
          params: {
            pdfUrl: this.urlPdfDescargar,
            correo: this.enailEnviar,
            idSolicitud: this.ruleForm._nro_hoja_reclamacion
          },
        }).then((response) => {
           this.$swal.fire({
              icon: 'success',
              title: 'Correo enviado con éxito',
              showConfirmButton: false,
              timer: 1500
            })
        });
      },
      soloNumeros: function(event){
        var key = window.event ? event.which : event.keyCode;
        if(key == 8){
        }else if (key < 48 || key > 57) {
          event.preventDefault();
        }
      },
    },
  };
</script>


